@use "../abstracts" as a;

/* JumpIn animation */
@keyframes JumpIn {
	20% {
		transform: translateY(-10px);
	}
	30% {
		transform: translateY(0px);
	}
	34% {
		transform: translateY(-5px);
	}
	38% {
		transform: translateY(0px);
	}
}

/* Flip Y animation */
@keyframes flipY {
	0% {
		transform: rotateY(0deg);
	}
	100% {
		transform: rotateY(360deg);
	}
}

/* Slide animation */
@keyframes slideLeftRating {
	0% {
		left: 0;
	}
	50% {
		left: -1000px;
	}
	100% {
		left: 0px;
	}
}

@keyframes slideRightRating {
	0% {
		left: -1000px;
	}
	50% {
		left: 0;
	}
	100% {
		left: -1000px;
	}
}

@keyframes slideLeft {
	0% {
		transform: translateX(100%);
	}
	100% {
		transform: translateX(0);
	}
}

@keyframes FadeInLeft{
	0% {
		opacity: 0;
		transform: translateX(-50%);
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

@keyframes FadeInRight{
	0% {
		opacity: 0;
		transform: translateX(50%);
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

@keyframes FadeInBottom{
	0% {
		opacity: 0;
		transform: translateY(50%);
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

@keyframes FadeInTop{
	0% {
		opacity: 0;
		transform: translateY(-100%);
	}
	100% {
		opacity: 1;
		transform: translateX(0);
	}
}

@keyframes FadeInCenter{
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes headerShow{
	0% {
		opacity: 0;
		transform: translateY(-100%);
	}
	100% {
		opacity: 1;
		transform: translateY(0%);
	}
}

@keyframes headerHide{
	0% {
		opacity: 0;
		transform: translateY(-100%);
	}
	100% {
		opacity: 1;
		transform: translateY(0%);
	}
}

@keyframes zoomCenter{
	0% {
		opacity: 0;
		transform: scale(0)
	}
	100% {
		opacity: 1;
		transform: scale(1)
	}
}

@property --num {
	syntax: "<integer>";
	initial-value: 90;
	inherits: false;
}

.counter-animation-90 {
	animation: counter 4.5s alternate ease-in-out;
	counter-reset: num var(--num);
}
.counter-animation-90::after {
	content: counter(num);
}

@keyframes counter {
	from {
		--num: 0;
	}
	to {
		--num: 90;
	}
}